
.contact {
  padding: 30px 20px;
  position: relative;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.7);
}
.contact:after {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.6;
    background: url("../../assets/aboutme/bg.jpg");
    background-position: center;
    background-size: cover;
}
.contact .container {
    max-width: 920px;
    color: #fff;
    margin: 0 auto;
    padding: 20px 20px;
}
.contact .container h1, .contact .container h2, .contact .container h4 {
      font-weight: bold;
      font-size: 44px;
      padding: 10px 0;
}
.contact .container h2 {
      font-size: 28px;
}
.contact .container h4 {
      font-size: 22px;
      font-weight: normal;
}
.contact .container p {
      font-size: 22px;
      padding: 32px 0;
      line-height: 1.0;
}
.contact .container form {
      width: 90%;
      margin: 20px 0px;
}
.contact .container input, .contact .container textarea {
      font-size: 1.5em;
      width: 100%;
      margin-bottom: 0.8em;
      padding: 20px 0px 20px 20px;
}
.contact .container textarea {
      overflow: auto;
      height: 200px;
      resize: none;
}
.contact .container button {
      font-size: 22px;
      background: white;
      padding: 20px;
      border: 3px solid rgba(0, 214, 90, 0.6);
}



